.menu-card{
    width: 270px;
    height: 350px;
    border: 4px solid black;
    border-radius: 30px;
    margin: 50px;
    padding: 10px;
    background-position: absolute;
}

.number{
    width: 30px;
    height: 30px;
    border: 2px solid black;
    border-radius: 50%;
    font-size: 14px;
    padding: 3px;
    text-align: center;
    background-color: gray;
}

